$text-dark-color:  #111 !default;

.PageNavigation {
  font-size: 14px;
  display: block;
  width: auto;
  overflow: hidden;
}

.PageNavigation a {
  display: block;
  width: 50%;
  float: left;
  margin: 1em 0;
}

.PageNavigation .next {
  text-align: right;
}

pre { padding: .75rem 1rem; }

pre.highlight {
  line-height: 1.15;

  code {
	font-size: .75rem;
  }
}

pre, code {
  background-color: #fff0f0;
}

.post-content {
  margin-bottom: $spacing-unit;

  h1 {
	@include relative-font-size(2.75);

	margin-top: 4rem;
	color: $text-dark-color;

	@include media-query($on-laptop) {
	  @include relative-font-size(2.5);

	  margin-top: 3rem;
	}

	&:first-child {
	  margin-top: 0;
	}
  }

  h2 {
	@include relative-font-size(2);

	margin-top: 2rem;

	@include media-query($on-laptop) {
	  @include relative-font-size(1.75);
	}
  }

  h3 {
	@include relative-font-size(1.625);

	@include media-query($on-laptop) {
	  @include relative-font-size(1.375);
	}
  }

  h4 {
	@include relative-font-size(1.25);

	@include media-query($on-laptop) {
	  @include relative-font-size(1.125);
	}
  }
}
