$base-font-family: Pretendard, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$text-color:       #fff;
$text-dark-color:  #fff;
$background-color: #333;
$brand-color:      #bbeeff;
$grey-color:       #999;
$grey-color-dark: lighten($grey-color, 40%) !default;
$grey-color-light:  darken($grey-color, 25%) !default;

@import "minima";
@import "sweetlittlebird";

.post-content h1,
.post-content h2,
.post-content h3,
.post-content h4,
.post-content h5 { border-bottom-color: #777; box-shadow: 0 1px 0 0 #333; }

pre, 
code { 
  background-color: #456;
  color: #fff;
}

body {
  --scrollbar-color-thumb: #999;
  --scrollbar-color-thumb-hover: #fff;
  --scrollbar-color-track: #252233;
  --scrollbar-width: thin;
  --scrollbar-width-legacy: 10px;
}

/* Modern browsers with `scrollbar-*` support */
@supports (scrollbar-width: auto) {
  body {
    scrollbar-color: var(--scrollbar-color-thumb) var(--scrollbar-color-track);
    scrollbar-width: var(--scrollbar-width);
  }
}

/* Legacy browsers with `::-webkit-scrollbar-*` support */
@supports selector(::-webkit-scrollbar) {
  ::-webkit-scrollbar-thumb {
    background: var(--scrollbar-color-thumb);
  }
  ::-webkit-scrollbar-thumb:hover {
    background: var(--scrollbar-color-thumb-hover);
  }
  ::-webkit-scrollbar-track {
    background: var(--scrollbar-color-track);
  }
  ::-webkit-scrollbar {
    max-width: var(--scrollbar-width-legacy);
    max-height: var(--scrollbar-width-legacy);
  }
}